<template>
    <div class="home">
        <div class="header">
            <img />
        </div>
        <div class="content">
            <div v-for="(blog) in blogs" v-bind:key="blog.fileName">
                <div class="title" @click="navToDetailPage(blog)">
                    {{ blog.title }}
                </div>

                <div class="blog-content-preview">
                    {{ blog.subtitle }}
                </div>
                <div class="blog-tags" v-if="blog.tags">
                    <div v-for="(tag) in blog.tags.split(',')" v-bind:key="tag" class="blog-tag">
                        {{ tag }}
                    </div>
                </div>
                <div class="blog-content-preview blog-date">
                    Posted by Tongfu on {{ blog.createDate }}
                </div>
                <hr />
            </div>
        </div>
    </div>
</template>
<script>
import { useBlogStore } from '../store/blogstore.js'
import { storeToRefs } from 'pinia'
export default {
    name: 'HomePage',
    data() {
        return {
            blogs: [],
            scrollTop: 0,
        }
    },

    mounted() {
        const blogStore = useBlogStore()
        const { blogList } = storeToRefs(blogStore)
        this.blogs = blogList
        console.log("blogList", this.scrollTop)
    },

    methods: {
        navToDetailPage(blog) {
            this.$router.push({
                name: 'detail',
                //传递blog参数到detail页面
                query: {
                    fileName: blog.fileName
                }
            })
        },
    },

    activated() {
        console.log("activated")
        document.querySelector('.content').scrollTop = this.scrollTop
        console.log("activated", this.scrollTop)
    },


    beforeRouteLeave(to, from, next) {
        console.log("beforeRouteLeave")
        //获取页面中content的scrollTop
        this.scrollTop = document.querySelector('.content').scrollTop
        console.log("scrollTop", this.scrollTop, to, from)
        next()
        console.log("beforeRouteLeave", this.scrollTop)
    }
}
</script>


